.App {
  text-align: center;
}

.App-logo {
  height: 80px;
  position:absolute;
  left:20px;
}
.footer-logo{
  height: 80px;
  float: center;
}

.App-header {
  background-color: #222;
  height: 70px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 2.5em;
}

.App-intro {
  font-size: large;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

footer{
  text-align: left;
  margin-top: 50px;
  border-top: solid black;
  padding: 25px 70px;
  height: 150px;
  display: -ms-flexbox;
  display: flex;
}

footer div{
  -ms-flex: 4 1 auto;
      flex: 4 1 auto;
  min-width: 300px;
}
.logo{
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  padding: 20px;
  width: 80px;
  min-width: auto;
}



